.serch input {
  width: 90%;
  height: 50px;
  border-radius: 20px;
  padding: 10px;
  display: block;
  margin: 20px auto;
}
.tour_list {
  max-width: 767px;
  width: calc(100% - 30px);
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 20px;
}
.tour_list h4 {
  font-family: 'Cafe24Supermagic-Bold-v1.0';
  font-size: 20px;
  margin-bottom: 15px;
}
.tour_list .item:nth-child(1) .item_text .place {
  display: none;
}
.tour_list .item:nth-child(2) .item_text .place {
  display: none;
}
.tour_list .item:nth-child(7) .item_text .place {
  display: none;
}
.tour_list .item {
  width: 40%;
  margin-top: 50px;
}
.list {
  border-radius: 20px;
  box-shadow: 10px 30px 20px #7caae734;
  position: relative;
}
.tour_list .item figure {
  width: 100%;
  height: 350px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
  }
}
.tour_list .item_text {
  width: 250px;
  height: 150px;
  font-size: 15px;
  padding: 20px;
  word-break: keep-all;
  align-items: center;
  background-color: #0188CC;
  color: aliceblue;
  border-radius: 10px 45px;
  position: absolute;
  bottom: -50px;
  left: -10px;
  font-family: 'Dovemayo_gothic';
}
.tour_list .item .place {
  display: flex;
  margin-top: 15px;
  h6 {
    width: 190px;
    font-size: 14px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}
.tour_list .item .place figure {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
footer {
  max-width: 767px;
  width: 100%;
  height: 100px;
  margin: 20px auto;
  background-color: #a2e0fd;
  font-size: 20px;
  border-radius: 30px;
  align-items: center;
  display: flex;
  justify-content: space-around;
  position: fixed;
  bottom: 0;
  font-family: 'Cafe24Supermagic-Bold-v1.0';
}
